.header {
  width: 100%;
  height: 80px;
  background: linear-gradient(45deg, #49a09d, #080232);

  .container {
    max-width: 1196px;
    height: 100%;
    margin: auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-left: 20px;
  }
}

.link {
  text-decoration: none;
  color: #fff;
  font-size: 15px;
  font-weight: bold;
  text-transform: uppercase;
  padding-left: 20px;
  opacity: .5;
}

.active {
  opacity: 1;
}

.logout {
  cursor: pointer;
  text-decoration: none;
  color: #fff;
  border: 1px solid #7f7272;
  border-radius: 4px;
  padding: 2px 4px;
  margin: 8px;
}

.logout:active {
  background: #fff;
  color: black;
}

.user {
  display: flex;
  align-items: center;
}

.user img {
  width: 40px;
  height: 40px;
  margin: 8px;
  border-radius: 50%;
  background: #fff;
}

.user span {
  font-size: 14px;
  line-height: 22px;
  font-weight: 600;
  color: #FFFFFF;
  margin-right: 8px;
}
